/* ************************
   03.6: Coming Soon
   ********************* */
   .coming-soon {
      background-size: cover;
      display: flex;
      align-items: center;
      padding-top: 100px;
      padding-bottom: 100px;
   }

   .coming-soon-content {
      @include tab {
         margin-bottom: 60px;
      }
      > img {
         margin-bottom: 14px;
      }

      h1 {
         font-size: 72px;
         line-height: 1.111;
         margin-bottom: 47px;
      }
      #countdown {
         margin-bottom: 14px;
      }
      p {
         margin-bottom: 26px;
         @include mobileLg {
            br {
               display: none;
            }
         }
      }
   }

   .coming-soon-newsletter {
      max-width: 480px;
      .form-control {
         border: 1px solid $white;
         border-right: 0;
         border-radius: 50px 0 0 50px;
      }

      .btn-wrap {
         span {
            @extend .white-bg;
         }
         .btn {
            @extend .c2-bg;
            border-radius: 0 50px 50px 0;
            &:after {
               @extend .white-bg;
            }
            &:hover {
               @extend .c1;
            }
         }
      }
   }
