/* ************************
   04.2: Solution
   ********************* */

.solution-img {
    position: relative;

    .s_man {
        position: absolute;
        left: 0;
        top: 130px;
        transition: .1s;
    }
    .s_woman {
        position: absolute;
        left: auto;
        right: 60px;
        top: 150px;
        transition: .1s;
    }
}

@include tab {
    .solution-img-responsive {
        margin-bottom: 60px;
    }
}

.single-solution {
    &:not(:last-child) {
        margin-bottom: 30px;
    }

    .img {
        background-color: rgba(85, 81, 239, .1);
        width: 90px;
        height: 90px;
        border-radius: 100%;
        margin-right: 30px;
        display: flex;
        align-items: center;
        justify-content: center;

        &.style--two {
            background-color: rgba(15, 231, 5, .1);
        }
        &.style--three {
            background-color: rgba(255, 255, 255, .09);
        }
    }

    .content {
        h3 {
            margin-bottom: 15px;
        }
    }
}