/* ************************
   02.7: Widget
   ********************* */
.widget {
    font-size: 16px;
    &:not(:last-child) {
        margin-bottom: 50px;
    }
    .widget-title {
        h3 {
            margin-bottom: 24px;
            position: relative;
            padding-left: 14px;
            &:after {
                width: 3px;
                height: 17px;
                @extend .c1-bg;
                left: 0;
                top: 3.5px;
                content: "";
                position: absolute;
            }
        }
    }
    ul {
        padding: 0;
        margin: 0;
        list-style: none;
    }
    &.widget_newsletter {
        .newsletter-content {
            p {
                margin-bottom: 25px;
            }
        }
        &.style--two {
            .newsletter-content {
                p {
                    font-size: 14px;
                }
                .theme-input-group button {
                    position: static;
                    width: 100%;
                    margin-top: 16px;
                    height: 52px;
                }
                .theme-input-group input {
                    background-color: $white;
                }
            }
        }
    }
    &.widget_contact {
        ul {
            li {
                padding-left: 30px;
                position: relative;
                font-size: 18px;
                &:not(:last-child) {
                    margin-bottom: 15px;
                }
                .icon {
                    position: absolute;
                    left: 0;
                }
                a {
                    @extend .black;
                    &:hover {
                        @extend .c2;
                    }
                }
            }
        }
    }
    &.widget_service {
        .service-list {
            @extend .nunito;
            li {
                line-height: 2;
                display: flex;
                align-items: center;
                a {
                    @extend .text-color;
                    &:hover {
                        @extend .c2;
                        i,
                        svg * {
                            @extend .c2;
                            fill: $c2;
                        }
                    }
                }
                i {
                    font-size: 18px;
                }
                img,
                svg,
                i {
                    margin-right: 10px;
                    @extend .trans3;
                }
                &:not(:last-child) {
                    margin-bottom: 24px;
                }
            }
        }
    }
    &.widget_recent_entries {
        ul {
            li {
                &:not(:last-child) {
                    margin-bottom: 23px;
                }
                .posted-on {
                    margin-bottom: 10px;
                    display: block;
                    i {
                        margin-right: 5px;
                    }
                    i, a {
                        color: $c2 !important;
                        font-size: 14px;
                    }
                }
                .post-title {
                    a {
                        line-height: 1.666;
                        @extend .text-color;
                        &:hover {
                            @extend .c1;
                        }
                    }
                }
            }
        }
    }
    &.widget_search,
    &.widget_newsletter.style--two {
        @extend .search-form;
    }
    &.widget_tag_cloud {
        .tagcloud {
            margin: 0 -6px -6px 0;
            a {
                display: inline-block;
                padding: 5px 8px;
                background-color: #F9F9F9;
                margin: 0 3px 6px 0;
                @extend .text-color;
                @extend .medium;
                border-radius: 2px;
                font-size: 13px;
                &:hover {
                    @extend .c1;
                }
            }
        }
    }
    &.widget_nav_menu {
        ul {
            li {
                a {
                    font-size: 18px;
                    text-transform: capitalize;
                    display: block;
                }
                &:not(:last-child) {
                    margin-bottom: 14px;
                }
            }
        }
    }
    &.widget_social_links {
        margin: 0;
        padding: 30px;

        .social-links {
            a {
                background-color: rgba(0, 0, 0, 0.05);
                @extend .black;
        
                &:hover {
                    @extend .white;
                    @extend .c2-bg;
                }
        
            }
        }
    }
    &.widget_categories {
        ul {
            li {
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
                a {
                    position: relative;
                    padding-left: 28px;
                    @extend .text-color;
                    display: block;

                    &:after {
                        position: absolute;
                        background-image: url(../img/icons/angle-right.svg);
                        background-repeat: no-repeat;
                        width: 6px;
                        height: 10px;
                        content: "";
                        left: 0;
                        top: 8px;
                    }

                    &:hover {
                        @extend .c1;
                    }
                }
            }
        }
    }
}