/* ************************
   04.4: Price
   ********************* */

    .pricing {
        overflow: hidden;
    }

    .single-price {
        @extend .box-shadow2;
        @extend .white-bg;
        @extend .radius-10;
        padding: 60px 20px;
        overflow: hidden;
        position: relative;
        margin-bottom: 30px;
        text-align: center;

        .ribbon {
                background-color: #0FE705;
                position: absolute;
                height: 50px;
                color: #fff;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                width: 200px;
                top: 28px;
                right: -48px;
                transform: rotate(45deg);
        }

        .price-head {
            margin-bottom: 30px;
            padding-bottom: 30px;
            position: relative;
            &:after {
                position: absolute;
                content: "";
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
                width: 60%;
                height: 1px;
                @extend .radius;
                @extend .black-bg;
                opacity: .1;
            }
        }

        .price {
            @extend .nunito;
            @extend .c1;
            line-height: 1;
            padding: 20px 0;
            @extend .semi-bold;
            @extend .trans3;
            .currency {
                vertical-align: top;
                font-size: 24px;
            }
            .value {
                    @extend .semi-bold;
                    font-size: 48px;
            }
        }

        .price-body {
            ul {
                @extend .list-unstyled;
                margin-bottom: 26px;

                li {
                    margin-bottom: 14px;

                    strong {
                        @extend .black;
                    }
                }
            }
        }

        &:hover {
            .price {
                @extend .c2;
            }
        }
        &.style--two {
            .ribbon {
                background-color: #00D7FF;
            }
            .price {
                @extend .black;
            }
            &:hover {
                .price {
                    color: #00D7FF;
                }
            }
            .btn-wrap {
                span {
                    background-color: #00D7FF;
                }
                .btn {
                    @extend .black-bg;
                    &:after {
                        background-color: #00D7FF;
                    }
                }
            }
        }
        &.style--three {
            .ribbon {
                background-color: #081F70;
            }
            .price {
                color: #081F70;
            }
            &:hover {
                .price {
                    @extend .c2;
                }
            }
            .btn-wrap {
                span {
                    background-color: #0CE177;
                }
                .btn {
                    background-color: #081F70;
                    &:after {
                        background-color: #0CE177;
                    }
                }
            }
        }
    }