/* ************************
   02.5: Section Title
   ********************* */
.section-title {
    margin-bottom: 65px;
    @include tab {
        margin-bottom: 46px;
    }
    h2 {
        font-size: 36px;
        line-height: 1.35;
        margin-bottom: 12px;
        @include mobileMd {
            font-size: 30px;
        }
    }
    @include tab {
        br {
            display: none;
        }
    }
    &.style--two {
        margin-bottom: 46px;
    }
}


.title-shape {
    position: relative;
    padding-top: 15px;
    &:before, &:after {
        position: absolute;
        left: 0;
        top: 0px;
        width: 60px;
        height: 3px;
        border-radius: 50px;
        content: "";
        background-color: #5551EF;
    }
    &:after {
        width: 20px;
        left: 30px;
        background-color: #fff;
    }
    &.text-center:before, &.text-center:after {
        left: 50%;
        margin-left: -30px;
    }
    &.text-center:after {
        margin-left: 0px;
    }
    &.text-right:before, &.text-right:after {
        right: 0px;
        left: auto;
    }
    &.text-right:after {
        margin-right: 10px;
    }

    &.title-shape-style-two {
        &:before {
            background-color: #fff;
        }
        &:after {
            background-color: #5551EF;
        }
    }

    &.title-shape-sky-blue {
        &:before {
            background-color: #00D7FF;
        }
        &.style--two {
            &:after {
                background-color: #003E7B;
            }
        }
    }
    &.title-shape-c2 {
        h2 {
            color: #081F70;
        }
        &.title-shape-style-two {
            &:before {
                background-color: #fff;
            }
            &:after {
                background-color: #0CE177;
            }
        }
        &:before {
            background-color: #0CE177;
        }
        &.style--two {
            &:after {
                background-color: #fff;
            }
        }
    }
}