/* ************************
   03.3: Blog
   ********************* */

   .single-blog {
      margin-bottom: 30px;
      display: inline-block;
      .blog-img {
         border-radius: 5px 5px 0 0;
         img {
            border-radius: 5px 5px 0 0;
            width: 100%;
         }
      }

      .blog-content {
         @extend .white-bg;
         @extend .box-shadow2;
         padding: 40px 40px 32px;
         position: relative;
         border-radius: 0 0 5px 5px;
         @include mobileSm {
            padding: 40px 20px 32px;
         }

         h3 {
            a {
               @extend .black;
            }
            margin-bottom: 16px;
         }
      }

      .blog-meta {
         position: absolute;
         top: 0px;
         left: 45px;
         @extend .c1-bg;
         padding: 3px 15px;
         border-radius: 50px;
         transform: translateY(-50%);
         @include mobileSm {
            left: 20px;
         }

         ul {
            li {
               a {
                  font-size: 14px;
                  @extend .white;
               }
            }
         }
      }

      &.style--two {
         .blog-meta {
            display: inline-block;
            position: static;
            margin-bottom: 16px;
            transform: none;
         }
      }

      &.style--three {
         .blog-meta {
            background-color: #00D7FF;
         }
         .btn-link {
            @extend .black;

            svg path {
               stroke: $black;
            }

            &:hover {
               color: #00D7FF;
               svg path {
                  stroke: #00D7FF;
               }
            }
         }
      }
      &.style--four {
         .blog-meta {
            background-color: #0CE177;
         }
         .btn-link {
            @extend .black;

            svg path {
               stroke: $black;
            }

            &:hover {
               color: #0CE177;
               svg path {
                  stroke: #0CE177;
               }
            }
         }
      }
   }


   // Newsletter Form Wrapper
   .news-letter-form-wrapper {
      background-image: radial-gradient(at center center, #7875F4 0%, #5551EF 100%);
      padding: 58px 39px;
      @extend .radius-5;
      @extend .box-shadow2;
      position: relative;
      margin-bottom: 30px;
      z-index: 1;
      @include mobileSm {
         padding: 48px 20px;
      }

      &:after {
         right: 20px;
         bottom: 0;
         width: 100px;
         height: 100px;
         position: absolute;
         content: "";
         z-index: -1;
         opacity: 0.1;
         background-image: url(../img/icons/newsletter-icon.png);
      }

      .newsletter-form {
         padding: 0 10px;
      }

      h3, p {
         @extend .white;
      }
      h3 {
         position: relative;
         padding-bottom: 28px;
         margin-bottom: 22px;

         &:after {
            width: 45px;
            height: 1px;
            @extend .white-bg;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            content: "";
         }
      }

      > p {
         margin-bottom: 21px;
      }
      .sectsubscribe-email {
         margin-bottom: 14px;
      }
      .btn:hover:after {
         transform: scale(90);
     }

      &.style--two {
         background-image: linear-gradient(219deg, #01064A 0%, #00D7FF 100%);

         .btn-wrap {
            span {
               background-color: #00D7FF;
            }
            .btn {
               color: #00D7FF;
               &:after {
                  background-color: #00D7FF;
               }
               &:hover {
                  color: $white;
               }
            }
         }
      }
      &.style--three {
         background-image: radial-gradient(at center center, #34F093 0%, #0CE177 100%);

         .btn-wrap {
            span {
               background-color: #081F70;
            }
            .btn {
               color: #081F70;
               &:after {
                  background-color: #081F70;
               }
               &:hover {
                  color: $white;
               }
            }
         }
      }
   }

   .sectsubscribe-email {
      border-radius: 50px;
      background-color: rgba(57, 62, 92, 0.6);
      padding: 0 15px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      color: #fff;
      border: none;
      font-size: 15px;
      width: 100%;

      &::placeholder {
         color: rgba(255, 255, 255, 0.5) !important;
      }
   }

   @include tab {
      .blog-sidebar {
         margin-top: 60px;
      }
   }


   /* Blog Details */
   .post-details {
      font-size: 16px;
      p {
         line-height: 1.86;
      }
      figure {
         margin-top: 50px;
         margin-bottom: 50px;
      }
      figcaption {
         margin-top: 15px;
      }
      .entry-header {
         .entry-thumbnail {
            margin-bottom: 47px;
         }
         .entry-title {
            font-size: 36px;
            line-height: 1.28;
            margin-bottom: 15px;
         }
         .entry-meta {
            margin-bottom: 30px;
            li {
                  &:not(:last-child) {
                     margin-right: 50px;
                  }
                  span {
                     font-weight: $semi-bold;
                     margin-right: 7px;
                     font-size: 16px;
                  }
                  a {
                     @extend .text-color;
                     font-size: 14px;
                     &:hover {
                        @extend .c2;
                     }
                  }
            }
         }
      }

      .entry-tags,
      .entry-categories {
         ul {
            li {
                  &:first-child {
                     @extend .nunito;
                     color: $black;
                     font-size: 16px;
                     font-weight: $bold;
                     margin-right: 5px;
                  }
                  a {
                     @extend .text-color;
                     font-size: 14px;
                     line-height: 1.57;
                     &:not(:last-child) {
                        &:after {
                              content: ',';
                        }
                     }
                     &:hover {
                        @extend .c1;
                     }
                  }
            }
         }
      }
   }

   /* Blog Details Content */
   .entry-content,
   .comment-content,
   .page--content {
      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
         line-height: 1.5;
         margin-bottom: 23px;
      }
      p {
         margin-bottom: 25px;
      }
      table {
         margin-bottom: 50px;
      }

      select {
         @extend .theme-input-style;
         margin-bottom: 50px;
      }
      
      > * {
         &:last-child {
            margin-bottom: 0;
         }
      }
      a {
         color: $black;
         font-weight: $bold;
         font-size: 14px;
         &:hover {
            text-decoration: underline;
         }
         i {
            margin-right: 5px;
         }
      }
   }

   /* Post Author Info */
   .post-author {
      padding: 40px 0;
      @include mobileMd {
         flex-direction: column;
      }
      .author-thumb {
         margin-right: 30px;
         @include mobileMd {
            margin-right: 0;
            margin-bottom: 30px;
         }
      }
      .author-info {
         h4 {
            font-size: 18px;
            margin-bottom: 10px;
            color: $black;
            font-weight: $semi-bold;
         }
         p {
            font-size: 16px;
            margin-bottom: 20px;
         }
      }
   }
   .post-title {
      color: $black;
   }
   /* Post Navigation */
   .post-navigation {
      .nav-link {
         flex-basis: 50%;
         max-width: 50%;
         padding: 0;
         .nav-title {
            display: block;
            font-size: 14px;
            @extend .bold;
            opacity: .5;
            margin-bottom: 17px;
            color: $black;
            @extend .trans3;
            i {
                  margin-right: 10px;
            }
            &.text-right {
                  i {
                  margin-left: 10px;
            }
            }
         }
         .posted-on {
            margin: 10px 0 2px;
            display: block;
            font-size: 14px;
            @extend .text-color;
         }
         &:hover {
            .nav-title {
                  opacity: 1;
            }
         }
      }
   }

   /* Related Post */
   .related-post {
      h2 {
         margin-bottom: 30px;
      }
      h4 {
         color: $black;
         margin-bottom: 30px;
      }
   }

   /* Post Comments */
   .comments-area {
      .comments-title {
         margin-bottom: 25px;
         color: $black;
      }
      ul {
         margin: 0;
         padding: 0;
         list-style: none;
      }
      .comment {
         .single-comment {
            padding-bottom: 30px;
            margin-bottom: 30px;
            border-bottom: 1px solid #E2E2E2;
            @include mobileSm {
                  flex-direction: column;
            }
            .comment-author-image {
                  min-width: 65px;
                  margin-right: 30px;
                  border-radius: 50%;
                  overflow: hidden;
                  @include mobileSm {
                     margin-right: 0;
                     margin-bottom: 30px;
                  }
            }
            &:not(.trackback):not(.pingback) {
                  .comment-content {
                     max-width: calc(100% - 95px);
                     width: 100%;
                     @include mobileSm {
                        max-width: 100%;
                     }
                  }
            }
            .comment-content {
                  .author_name {
                     margin-right: 10px;
                     margin-bottom: 0;
                     font-size: 16px;
                  }
                  .commented-on {
                     font-size: 12px;
                  }
                  p {
                     margin: 7px 0 5px;
                     font-size: 16px;
                     color: $text-color;
                     font-weight: $regular;
                  }
            }
         }
         ul.children {
            margin-left: 95px;
            @include mobileSm {
                  margin-left: 20px;
            }
         }
      }
   }

   /* Comments Form */
   .comment-respond {
      @include tab {
         margin-bottom: 60px;
      }
      .comment-reply-title {
         margin-bottom: 20px;
         color: $black;
      }
      .comment-form {
         .comment-notes {
            margin-bottom: 30px;
            font-size: 16px;
         }
         .theme-input-style {
            border-radius: 25px;
            border: 1px solid #EEEEEE;
            @extend .text-color;
            padding: 10px 20px;
            &:focus {
                  background-color: #F1F3FF;
            }
         }
         label {
            margin-bottom: 18px;
         }
         .custom-checkbox {
            margin-bottom: 5px;
            label {
               font-size: 16px;

               input[type=checkbox] {
                  margin-right: 8px;
               }
            }
         }
      }
   }
