/* ************************
   04.6: Testimonial
   ********************* */

   .testimonial-carousel.owl-carousel {
      .owl-stage-outer {
         margin: -25px;
         padding: 25px;
      }
   }

   .single-testimonial {
      box-shadow: 0px 0px 20px rgba(34, 34, 34, 0.1);
      @extend .white-bg;
      @extend .radius-10;
      padding: 54px 45px 45px;
      margin-bottom: 30px;
      position: relative;
      z-index: 1;

      @include mobileSm {
         padding: 44px 15px 35px;
      }

      .quote {
         width: 50px;
         height: 50px;
         @extend .radius-5;
         @extend .c1-bg;
         position: absolute;
         left: 45px;
         top: -25px;
         display: inline-flex;
         justify-content: center;
         align-items: center;
         @include mobileSm {
            left: 15px;
         }
      }

      .testimonial-content {
         margin-bottom: 22px;
      }

      .testimonial-img {
         margin-right: 20px;
         img {
            @extend .radius;
            width: 65px;
            height: 65px;
         }
      }

      .testimonial-name {
         h4 {
            margin-bottom: 5px;
         }
         span {
            display: inline-block;
            @extend .c1;
         }
      }

      .rating {
         i {
            color: #FFD643;
            font-size: 13px;
            &:not(:last-child) {
               margin-right: 3px;
            }
         }
      }

      &:after {
         background-image: url(../img/media/big-quote.png);
         content: "";
         right: 10px;
         bottom: 0;
         position: absolute;
         width: 162px;
         height: 135px;
         z-index: -1;
         opacity: 0.03;
      }
   }


   .testimonial-carousel.style--two {
      .single-testimonial {   
         .quote {
            background-color: #00D7FF;
         }
   
         .testimonial-name {
            span {
               color: #00D7FF;
            }
         }
      }
   }

   .testimonial-carousel.style--three {
      .single-testimonial {   
         .quote {
            background-color: #0CE177;
         }
   
         .testimonial-name {
            span {
               color: #0CE177;
            }
         }
      }
   }