/* ************************
   02.4: Buttons
   ********************* */

.btn-wrap {
    display: inline-block;
    position: relative;
    border-radius: 50px;

    span {
        display: inline-block;
        background-color: #0FE705;
        position: absolute;
        content: "";
        width: 9px;
        height: 9px;
        border-radius: 50%;
        right: 3px;
        bottom: 5px;
        transition: all 1.5s ease;
        opacity: 1;
        z-index: 9;
    }
    .btn {
        @extend .btn;
    }

    &:hover span {
        opacity: 0;
        transition: all .5s ease;
    }
    &.style--two {
        span {
            background-color: $black;
        }
        .btn {
            background-color: #00D7FF;
            &:after {
                background-color: $black;
            }
        }
    }
}

.btn {
    font-size: 16px;
    text-transform: uppercase;
    @extend .white;
    position: relative;
    z-index: 1;
    font-weight: 700;
    @extend .c1-bg;
    border-radius: 50px;
    line-height: 1;
    font-family: "Nunito Sans", sans-serif;
    padding: 18px 39px;
    transition: .7s;
    border: none;
    overflow: hidden;
    box-shadow: none;
    outline: none;

    &:after {
        background-color: #0FE705;
        position: absolute;
        content: "";
        z-index: -1;
        width: 9px;
        height: 9px;
        border-radius: 50%;
        right: 3px;
        bottom: 5px;
        transition: all .7s ease;
        transform: scale(1);
        transition-delay: .1s;
    }

    &:hover {
        color: #fff;

        &:after {
            transform: scale(60);
        }
    }

    &.btn-white {
        color: $c2;
        @extend .white-bg;
        &:hover {
            @extend .white;
        }
    }
    &.btn-sm {
        font-size: 12px;
        padding: 14px 26px;
    }
}

.btn-link {
    @extend .c1;
    @extend .trans5;
    @extend .nunito;
    @extend .bold;
    text-transform: uppercase;
    font-size: 13px;

    svg {
        position: relative;
        left: 3px;
        top: -2px;
        @extend .trans5;
        path {
            @extend .trans3;
        }
    }

    &:hover {
        text-decoration: none;
        @extend .c1;
        svg {
            left: 7px;
        }
    }
    &.style--two {
        font-size: 14px;
        svg {
            left: 0px;
        }
        &:hover {
            svg {
                left: -5px;
            }
        }
    }
}