/* ************************
   03.5: 404
   ********************* */

   .search-form {
      &.style--two {
         max-width: 500px;
         margin: 0 auto;
         position: relative;
         input {
            height: 60px;
            @extend .c1-bg;
            @extend .radius-50;
            @extend .white;
         }
         button {
            position: absolute;
            right: 16px;
            top: 50%;
            transform: translateY(-50%);
         }
      }
   }

   .bg-404 {
      background-size: contain;
      display: flex;
      align-items: center;
      padding-top: 100px;
      padding-bottom: 100px;
   }

   .content-404 {
      h1 {
         color: #3C4858;
         margin-bottom: 26px;
      }

      > p {
         margin-bottom: 42px;
         font-size: 18px;
         @extend .black;
      }
   }
