/* ************************
   04.7: Service Point
   ********************* */

.service_point-content {
    @include tab {
        margin-bottom: 80px;
    }
}

.service_point-map-img {
    position: relative;
    margin-left: -30px;
    img {
        max-width: none;
    }
    @include medium {
        display: inline-block;
        margin-left: 0;
        img {
            max-width: 100%;
        }
    }
    .l_info {
        position: absolute;
        left: 45px;
        top: 35px;

        &.l_info2 {
            left: 34%;
            top: auto;
            bottom: 32%;
        }
        &.l_info3 {
            left: 41%;
	        top: 10px;
        }
        &.l_info4 {
            left: 57%;
	        top: 46%;
        }
        &.l_info5 {
            left: auto;
            right: -15px;
            top: 25px;
        }
        &.l_info6 {
            left: auto;
            top: auto;
            right: 0%;
            bottom: 22%;
        }

        @include medium {
            left: 35px;
            top: 30px;

            &.l_info2 {
                left: 30%;
                top: auto;
                bottom: 32%;
            }
            &.l_info3 {
                left: 35%;
                top: 3px;
            }
            &.l_info4 {
                left: 50%;
                top: 45%;
            }
            &.l_info5 {
                left: auto;
                right: 60px;
                top: 22px;
            }
            &.l_info6 {
                left: auto;
                top: auto;
                right:10%;
                bottom: 23%;
            }
        }

        .circle-ball {
            display: inline-block;
            width: 10px;
            height: 10px;
            background-color: #00D7FF;
            border-radius: 50%;
            z-index: 1;
            position: relative;
            cursor: pointer;
            &:after {
                position: absolute;
                content: "";
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                background-color: #00D7FF;
                border-radius: 50%;
                z-index: -1;
                transform: scale(1);
                animation: ripple2 2s 0s infinite;
            }
        }

        .info-box {
            background-color: #fff;
            padding: 20px 60px;
            border-radius: 5px;
            box-shadow: 10px 10px 60px rgba( 0, 0, 0,0.1 );
            position: absolute;
            width: max-content;
            transform: translateX(-50%);
            left: 0;
            bottom: 20px;
            transition: .4s;
            opacity: 0;
            visibility: hidden;
            z-index: 9;
            min-width: 240px;

            h3 {
                margin-bottom: 6px;
            }
            span {
                color: #00D7FF;
            }
        }
        
        &.active {
            .info-box {
                opacity: 1;
                visibility: visible;
                bottom: 30px;
            }
        }
    }
    @include medium {
        .dvpn_map-img {
            left: 25px;
            img {
                max-width: initial!important;
            }
        }
    }
}