/* ************************
   03.2: Banner
   ********************* */

   .banner {
      background-image: url(../img/media/bg_shape.png);
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      overflow-x: hidden;

      display: flex;
      align-items: center;
      min-height: 1000px;
      padding-top: 160px;
      padding-bottom: 60px;
      position: relative;
      z-index: 1;

      .banner-shape {
         position: absolute;
         z-index: -1;
         left: 0;
         top: 0;
      }
   }

   @include IE {
      .container {
         width: 1140px;
      }
   }

   .banner-content {
      @extend .white;
      h4, h1 {
         @extend .white;
      }
      h4 {
         text-transform: uppercase;
         margin-bottom: 20px;
      }
      h1 {
         line-height: 1.083;
         margin-bottom: 30px;
         font-size: 72px;
         letter-spacing: -2px;
         @include mobileMd {
            font-size: 48px;
            br {
               display: none;
            }
         }
      }

      p {
         font-size: 18px;
         line-height: 1.666;
         padding-left: 16px;
         border-left: 1px solid;
         margin-bottom: 34px;
         @include mobileMd {
            br {
               display: none;
            }
         }
      }
      @include tab {
         margin-bottom: 40px;
      }
   }

   .banner-img {
      position: relative;
      margin-right: -150px;
      .setting-img,
      .sheild-img,
      .lock-img,
      .box-img,
      .setting2-img,
      .card-img,
      .check-img {
         position: absolute;
         left: 40px;
         top: 70px;
         transition: .1s;
      }
      .main-img {
         max-width: initial;
         position: relative;
         left: -70px;
      }
      .setting-img {
         left: -120px;
         top: 280px;
         animation: slideLeft 1.3s ease 0s 1 forwards;
      }
      .sheild-img {
         animation: slideInTop 1s ease 0s 1 forwards;
      }
      .lock-img {
         top: auto;
         left: 180px;
         bottom: 20px;
         animation: matrix 5s ease infinite;
      }
      .card-img {
         top: auto;
         left: 330px;
         bottom: -50px;
         max-width: 255px;
         animation: slideInBottom 1.2s ease 0s 1 forwards;
      }
      .setting2-img {
         top: auto;
         left: auto;
         right: 50px;
         bottom: 230px;
         animation: slideRight 1.1s ease 0s 1 forwards;
      }
      .check-img {
         top: 160px;
         left: 369px;
         animation: slideInTop2 1.1s ease 0s 1 forwards;
      }
      .box-img {
         top: 390px;
         left: -37px;
         animation: slideInBottom2 1s ease 0s 1 forwards;
      }
   }

   .banner-img-responsive {
      margin-left: -80px;
      margin-right: -80px;

      @include mobileMd {
         margin: 0;
      }
   }

   .banner-btn-group {
      display: flex;
      align-items: center;

      .video-btn {
         margin-left: 16px;
      }
      @include mobileMd {
         flex-direction: column;
         align-items: flex-start;
         .video-btn {
            margin-top: 14px;
            margin-left: 0px;
         }
      }
   }

   @keyframes slideLeft {
      0% {
         left: -2000px;
      }
   
      100% {
         left: -120px;
      }
   }

   @keyframes slideInTop {
      0% {
         top: -2000px;
      }
   
      100% {
         top: 70px;
      }
   }
   @keyframes slideInTop2 {
      0% {
         top: -2000px;
      }
   
      100% {
         top: 160px;
      }
   }
   @keyframes slideInBottom {
      0% {
         bottom: -2390px;
      }
   
      100% {
         bottom: -50px;
      }
   }
   @keyframes slideInBottom2 {
      0% {
         top: 2390px;
      }
   
      100% {
         top: 390px;
      }
   }
   @keyframes slideRight {
      0% {
         left: auto;
         right: -2000px;
      }
   
      100% {
         left: auto;
         right: 50px;
      }
   }


   .banner.style--two {
      background-color: transparent;
      background-image: linear-gradient(67deg, #00D7FF -30%, #01064A 100%);
      min-height: 830px;
      padding-top: 30px;
      padding-bottom: 0px;
      position: relative;
      z-index: 1;
      @include tab {
         padding-top: 80px;
      }

      .banner-shape {
         position: absolute;
         left: 5%;
         top: 20%;
         z-index: -1;
      }

      .banner-content {
         .btn-wrap {
            span {
               @extend .white-bg;
            }
            .btn {
               background-color: #00D7FF;
               @extend .white;
               &:after {
                  @extend .white-bg;
               }
               &:hover {
                  color: #00D7FF;
               }
            }
         }
      }

      .banner-img {
         margin: 0;
         img {
            max-width: none;
            margin-left: -110px;
            @include customMq(992px, 1400px) {
               width: 150%;
            }
            @include tab {
               max-width: 100%;
               width: 100%;
               margin: 0;
            }
         }
      }
   }

   .banner.style--three {
      background-color: transparent;
      background-image: radial-gradient(at center center, #34F093 0%, #0CE177 100%);
      min-height: 964px;

      .banner-img {
         @include medium {
            margin-right: 0;
         }
      }

      .banner-btn-group {
         .btn-wrap {
            @include tab {
               margin-bottom: 14px;
            }
            span {
               background-color: $white;
            }
            .btn {
               background-color: #081F70;
               &:after {
                  background-color: $white;
               }
               &:hover {
                  color: #081F70;
               }
            }
            &:not(:last-child) {
               margin-right: 20px;
            }
            &:nth-child(2) {
               span {
                  background-color: #081F70;
               }
               .btn {
                  background-color: $white;
                  color: #081F70;
                  &:after {
                     background-color: #081F70;
                  }
                  &:hover {
                     color: $white;
                  }
               }
            }
         }
      }
   }