/* ************************
   02.6: Page Title
   ********************* */

.page-title-wrap {
    min-height: 530px;
    background-size: cover;
    padding-top: 180px;
    padding-bottom: 180px;
    display: flex;
    align-items: center;
    @include mobileSm {
        min-height: 440px;
    }
}

.page-title {
    h2 {
        margin-top: -13px;
        margin-bottom: 8px;
        font-size: 48px;
        line-height: 1.375;
    }
    ul {
        margin-bottom: -8px;
        li {
            font-size: 18px;
            font-weight: $bold;
            @extend .nunito;
            position: relative;
            z-index: 1;
            &:not(:last-child) {
                padding-right: 15px;
                margin-right: 10px;
                &:after {
                    right: 0;
                    position: absolute;
                    content: '';
                    top: 50%;
                    transform: translateY(-50%);
                    width: 6px;
                    height: 6px;
                    @extend .radius;
                    @extend .white-bg;
                    
                }
            }
            &.active {
                &:after {
                    position: absolute;
                    content: "";
                    left: 0;
                    bottom: 7px;
                    @extend .c2-bg;
                    width: 100%;
                    height: 3px;
                    z-index: -1;
                }

            }
        }
    }
}