/* ************************
   04.1: Feature
   ********************* */

    .single-feature {
        @extend .white-bg;
        @extend .box-shadow2;
        @extend .radius-10;
        @extend .ov-hidden;
        position: relative;
        padding: 60px 45px 55px;
        margin-bottom: 30px;

        .feature-icon {
            margin-bottom: 30px;
            img {
                @extend .trans7;
            }
        }

        .feature-content {
            h3 {
                margin-bottom: 16px;
            }
        }
        &:after {
            content: "01";
            position: absolute;
            right: -29px;
            top: -68px;
            font-size: 150px;
            @extend .extra-bold;
            @extend .nunito;
            @extend .black;
            opacity: 0.03;
        }
        &.two:after {
            content: "02";
        }
        &.three:after {
            content: "03";
        }

        &:hover {
            .feature-icon {
                img {
                    transform: rotate(5deg);
                }
            }
        }
    }