  font-family: "Nunito Sans", sans-serif;
  line-height: 1;
  color: #fff;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
}

.video-btn i {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background-color: #0FE705;
  color: #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-right: 16px;
  transition: .3s;
}

.video-btn:hover {
  color: #fff;
}

.video-btn:hover i {
  color: #5551EF;
}

.video-btn.style--two {
  position: relative;
  z-index: 1;
}

.video-btn.style--two i {
  width: 80px;
  height: 80px;
  margin: 0;
}

.video-btn.style--two:after {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  transform: scale(1);
  opacity: 0;
  animation: ripple 1.4s ease-in 0s infinite forwards;
  z-index: -1;
}

/* Owl Dots */
.owl-carousel .owl-dots {
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  display: flex;
  bottom: 0;
}

.owl-carousel button.owl-dot {
  margin: 0 7.5px;
  width: 7px;
  height: 7px;
  display: block;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: rgba(85, 81, 239, 0.2);
}

.owl-carousel button.owl-dot.active {
  background-color: #5551ef;
  width: 9px;
  height: 9px;
}

.owl-carousel button.owl-dot.active:after {
  content: "";
  position: absolute;
  background-color: rgba(85, 81, 239, 0.2);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform: scale(2);
}

.owl-carousel.style--two button.owl-dot {
  background-color: rgba(0, 215, 255, 0.3);
}

.owl-carousel.style--two button.owl-dot.active {
  background-color: #00d7ff;
}

.owl-carousel.style--two button.owl-dot.active:after {
  background-color: rgba(0, 215, 255, 0.15);
}

.owl-carousel.style--three button.owl-dot {
  background-color: rgba(15, 231, 5, 0.3);
}

.owl-carousel.style--three button.owl-dot.active {
  background-color: #0fe705;
}

.owl-carousel.style--three button.owl-dot.active:after {
  background-color: rgba(15, 231, 5, 0.15);
}

/* Theme Input Group */
.theme-input-group {
  position: relative;
  width: 100%;
}

.theme-input-group input {
  width: 100%;
  height: 50px;
  border: none;
  padding-left: 15px;
  padding-right: 50px;
  color: #ffffff;
  background-color: #334E95;
}

.theme-input-group button {
  position: absolute;
  top: 4px;
  right: 4px;
  height: calc(100% - 8px);
  padding: 9px 28px;
  line-height: 1;
  font-weight: 600;
  color: #ffffff;
}

.theme-input-group button:hover {
  background-color: #fb9389;
}

/* Theme Input Style */
.form-control {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.09);
  background-color: transparent;
  height: 52px;
  width: 100%;
  padding: 0 20px;
  font-size: 16px;
  margin-bottom: 30px;
  border-radius: 0;
}

.form-control:focus {
  background-color: rgba(0, 0, 0, 0.01);
  box-shadow: none;
}

textarea.form-control {
  height: 170px;
  padding: 20px 20px;
}

/* Others */
.ov-hidden, .hover-effect, .single-team, .single-feature {
  overflow: hidden;
}

.ovx-hidden, section {
  overflow-x: hidden;
}

/* Pagination */
.pagination li:not(:last-child) {
  margin-right: 5px;
}

.pagination li a,
.pagination li span {
  width: 50px;
  height: 50px;
  line-height: 1;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(85, 81, 239, 0.1);
}

.pagination li a.current,
.pagination li span.current {
  background-color: #0FE705;
}

.pagination li a:hover, .pagination li a.active,
.pagination li span:hover,
.pagination li span.active {
  color: #ffffff;
}

.pagination li a:hover svg path, .pagination li a.active svg path,
.pagination li span:hover svg path,
.pagination li span.active svg path {
  stroke: #fff;
  fill: #fff;
}

.pagination li.nav-btn a.disabled {
  cursor: not-allowed;
  opacity: .5;
}

/* Blockquote */
blockquote {
  max-width: 570px;
  margin: 0 auto 30px;
}

blockquote P {
  font-size: 18px;
  line-height: 1.67;
  font-style: italic;
  text-decoration: underline;
  margin-bottom: 15px !important;
}

blockquote cite {
  font-style: normal;
  font-size: 16px;
  position: relative;
  padding-left: 20px;
  line-height: 1;
}

blockquote cite:after {
  position: absolute;
  content: '';
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 3px;
  width: 10px;
}

/* Form Response */
.form-response {
  background-color: rgba(85, 81, 239, 0.2);
}

.form-response span {
  display: block;
  margin-top: 20px;
  margin-bottom: 30px;
  padding: 10px 15px;
}

/* Search Form */
.search-form .theme-input-group, .widget.widget_search .theme-input-group, .widget.widget_newsletter.style--two .theme-input-group {
  border: 1px solid #5551EF;
}

.search-form .theme-input-group input, .widget.widget_search .theme-input-group input, .widget.widget_newsletter.style--two .theme-input-group input {
  background-color: transparent;
}

.search-form .theme-input-group input::placeholder, .widget.widget_search .theme-input-group input::placeholder, .widget.widget_newsletter.style--two .theme-input-group input::placeholder {
  color: rgba(110, 110, 110, 0.45) !important;
}

.search-form .theme-input-group input:focus, .widget.widget_search .theme-input-group input:focus, .widget.widget_newsletter.style--two .theme-input-group input:focus {
  background-color: transparent;
}

.search-form .theme-input-group button, .widget.widget_search .theme-input-group button, .widget.widget_newsletter.style--two .theme-input-group button {
