/* Gutter 50 */
@media only screen and (min-width: 992px) {
  .gutter-50 {
    margin-left: -25px;
    margin-right: -25px;
  }
  .gutter-50 > [class*="col-"] {
    padding-left: 25px;
    padding-right: 25px;
  }
}

/* ************************
   02.2: Animations
   ********************* */
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -40px, 0);
    transform: translate3d(0, -40px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 40px, 0);
    transform: translate3d(0, 40px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@keyframes matrix {
  0% {
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  25% {
    transform: matrix(1, 0, 0, 1, 15, 20);
  }
  50% {
    transform: matrix(1, 0, 0, 1, -5, 25);
  }
  75% {
    transform: matrix(1, 0, 0, 1, -15, 15);
  }
  100% {
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@-webkit-keyframes slideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

@keyframes slideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

.slideOutUp {
  -webkit-animation-name: slideOutUp;
  animation-name: slideOutUp;
}

@-webkit-keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.slideInDown {
  -webkit-animation-name: slideInDown;
  animation-name: slideInDown;
}

@keyframes inner-circles-loader {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes ripple {
  0% {
    transform: scale(1);
    opacity: .3;
  }
  100% {
    transform: scale(1.4);
    opacity: 0;
  }
}

/* ************************
   02.3: Preloader
   ********************* */
/* Preloader CSS */
.preloader {
  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333366;
  z-index: 99999;
}

.box {
  width: 20px;
  height: 20px;
  display: inline-block;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.box:nth-child(1) {
  background: #ffa372;
  animation-name: box-one;
}

.box:nth-child(2) {
  background: #ed6663;
  animation-name: box-two;
}

.box:nth-child(3) {
  background: #b52b65;
  animation-name: box-three;
}

.box:nth-child(4) {
  background: #f9ed69;
  animation-name: box-four;
}

.box:nth-child(5) {
  background: #916dd5;
  animation-name: box-five;
}

.box:nth-child(6) {
  background: #b83b5e;
  animation-name: box-six;
}

/***********
BOX-ONE
************/
@keyframes box-one {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  50% {
    -webkit-transform: translateX(75px);
    transform: translateX(75px);
  }
  75% {
    -webkit-transform: translateX(75px) scale(0);
    transform: translateX(75px) scale(0);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}

/***********
BOX-TWO
************/
@keyframes box-two {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  50% {
    -webkit-transform: translateX(75px);
    transform: translateX(75px);
  }
  75% {
    -webkit-transform: translateX(75px) scale(0);
    transform: translateX(75px) scale(0);
  }
  100% {
    -webkit-transform: translateX(0px) scale(0);
    transform: translateX(0px) scale(0);
  }
}

/***********
BOX-THREE
************/
@keyframes box-three {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  25% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
  50% {
    -webkit-transform: translateX(10px) translateY(-25px) scale(0);
    transform: translateX(10px) translateY(-25px) scale(0);
  }
  75% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

/***********
BOX-FOUR
************/
@keyframes box-four {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(-25px);
    transform: translateY(-25px);
  }
  50% {
    -webkit-transform: translateY(-55px) scale(0);
    transform: translateY(-55px) scale(0);
  }
  75% {
    -webkit-transform: translateY(-25px);
    transform: translateY(-25px);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

/***********
BOX-FOUR
************/
@keyframes box-five {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  25% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  50% {
    -webkit-transform: translateX(-25px) translateY(-25px) scale(0);
    transform: translateX(-25px) translateY(-25px) scale(0);
  }
  75% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

/***********
BOX-SIX
************/
@keyframes box-six {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  50% {
    -webkit-transform: translateX(-75px);
    transform: translateX(-75px);
  }
  75% {
    -webkit-transform: translateX(-75px) scale(0);
    transform: translateX(-75px) scale(0);
  }
  100% {
    -webkit-transform: translateX(0px) scale(0);
    transform: translateX(0px) scale(0);
  }
}

/* ************************
   02.4: Buttons
   ********************* */
.btn-wrap {
  display: inline-block;
  position: relative;
  border-radius: 50px;
}

.btn-wrap span {
  display: inline-block;
  background-color: #0FE705;
  position: absolute;
  content: "";
  width: 9px;
  height: 9px;
  border-radius: 50%;
  right: 3px;
  bottom: 5px;
  transition: all 1.5s ease;
  opacity: 1;
  z-index: 9;
}

.btn-wrap:hover span {
  opacity: 0;
  transition: all .5s ease;
}

.btn-wrap.style--two span {
  background-color: #252525;
}

.btn-wrap.style--two .btn {
  background-color: #00D7FF;
}

.btn-wrap.style--two .btn:after {
  background-color: #252525;
}

.btn, .btn-wrap .btn {
  font-size: 16px;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  font-weight: 700;
  border-radius: 50px;
  line-height: 1;
  font-family: "Nunito Sans", sans-serif;
  padding: 18px 39px;
  transition: .7s;
  border: none;
  overflow: hidden;
  box-shadow: none;
  outline: none;
}

.btn:after, .btn-wrap .btn:after {
  background-color: #0FE705;
  position: absolute;
  content: "";
  z-index: -1;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  right: 3px;
  bottom: 5px;
  transition: all .7s ease;
  transform: scale(1);
  transition-delay: .1s;
}

.btn:hover, .btn-wrap .btn:hover {
  color: #fff;
}

.btn:hover:after {
  transform: scale(60);
}

.btn.btn-white {
  color: #0FE705;
}

.btn.btn-sm {
  font-size: 12px;
  padding: 14px 26px;
}

.btn-link {
  text-transform: uppercase;
  font-size: 13px;
}

.btn-link svg {
  position: relative;
  left: 3px;
  top: -2px;
}

.btn-link:hover {
  text-decoration: none;
}

.btn-link:hover svg {
  left: 7px;
}

.btn-link.style--two {
  font-size: 14px;
}

.btn-link.style--two svg {
  left: 0px;
}

.btn-link.style--two:hover svg {
  left: -5px;
}

